/* > Roboto */

// Note: normal + bold
@font-face {
    font-family: 'roboto';
    src: url('../fonts/roboto/Roboto_gdi.eot');
    src: url('../fonts/roboto/Roboto_gdi.eot?#iefix') format('embedded-opentype'), 
    url('../fonts/roboto/Roboto_gdi.woff') format('woff'), 
    url('../fonts/roboto/Roboto_gdi.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'roboto';
    src: url('../fonts/roboto/Roboto-Bold_gdi.eot');
    src: url('../fonts/roboto/Roboto-Bold_gdi.eot?#iefix') format('embedded-opentype'), 
    url('../fonts/roboto/Roboto-Bold_gdi.woff') format('woff'), 
    url('../fonts/roboto/Roboto-Bold_gdi.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'playfair-bold';
    src: url('../fonts/Playfair_Display/PlayfairDisplay-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: 'playfair-regular';
    src: url('../fonts/Playfair_Display/PlayfairDisplay-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'Sedgwick_Ave_Display';
    src: url('../fonts/Sedgwick_Ave_Display/SedgwickAveDisplay-Regular.ttf') format('truetype');
    // font-weight: normal;
    // font-style: normal;
}