// ====================================
// Article page's style
// ====================================

/*
* TABLE OF CONTENTS
*
* header
* social floating bar
* meta
* sapo
* body
*    -> caption
*    -> timeline block
* tag
*/




// end demo style

.site-header {
    background-color: #FF6600;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;

    div[class^="col"] {
        padding: 0;
    }

    .aside-header {
        flex: 0 0 50px;

        @include media-breakpoint-up(lg) {
            flex: 0 0 90px;
        }

        font-size: 0;
    }

    .main-header {
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 10px 0 5px !important;

        @include media-breakpoint-up(lg) {
            height: 90px;
        }

        justify-content: space-between;
    }

    .go-back {
        width: 55px;
        height: 50px;

        @include media-breakpoint-up(lg) {
            width: 90px;
            height: 90px;
        }

        border: none;

        &.has-touched,
        &:hover {
            background-color: darken(#FF6600, 10%);
        }

        @include media-breakpoint-down(md) {
            i {
                transform: scale(0.5);
                margin-left: -15px;
                margin-top: -20px;
            }

        }
    }

    .logo {
        display: inline-block;
        width: 40px;
        height: 45px;

        @include media-breakpoint-up(lg) {
            width: 205px;
            height: 50px;
        }

        margin-right: 10px;
        margin-top: 5px;
        background: url(../img/kt-logo.png) 0 0 no-repeat;
    }

    .topic-title {
        font-size: 2.4rem;
        text-align: center;
        font-family: $font-family-serif;
        text-transform: uppercase;

        a {
            color: #fff;
        }

        @include media-breakpoint-down(sm) {
            display: none;
        }
    }
    .group-title{
        @include media-breakpoint-down(lg){
            display: none;
        }
    }
    .group-link {
        margin-top: 8px;

        a {
            display: inline-block;
            padding: 3px 20px;
            border-radius: 3px;
            color: #fff;

            @include media-breakpoint-up(md) {
                margin: 0 20px;
            }
        }

        a:hover,
        .active {
            // border: 1px solid darken($primary, 10%);
            background-color: rgba(black, .1);
        }

        @include media-breakpoint-down(md) {
            width: 120px;
        }
    }

    .social-comment {
        margin-left: 10px;
        width: 100px;

        @include media-breakpoint-up(md) {
            width: 120px;
        }

        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            width: 30px;
            height: 30px;
            background-color: #0C4C9B;
            border-radius: 3px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

.article {
    margin-top: 50px;

    @include media-breakpoint-up(lg) {
        margin-top: 90px;
    }

    @include clearfix();

    &__cover {
        margin-bottom: $spacer-3;

        @include media-breakpoint-up(sm) {
            margin-bottom: $spacer-5;
        }

        text-align: center;
    }

    /*> header*/
    &__header {
        @include clearfix();
        font-size: 1.8rem;

        @include media-breakpoint-up(sm) {
            @include font-style(32,42);
        }

        color: black;
        text-align: center;
        text-transform: uppercase;
        font-family: $font-family-serif;
    }

    .body-author {
        font-weight: bold;
        text-align: right;

        // @include media-breakpoint-up(md) {
        //     width: 64rem;
        // }
        font-size: 1.6rem;
        margin: $spacer-4 auto 0;
    }


    /*> sapo*/
    &__sapo {
        font-size: 1.4rem;
        margin-top: $spacer-3;

        @include media-breakpoint-up(sm) {
            margin-top: $spacer-5;
            @include font-style(18,26);
        }

        font-weight: bold;
    }

    /*> body*/
    &__body {
        // Note: padding, margin, fontstyle among article body
        overflow: hidden;
        overflow-wrap: break-word;
        @include clearfix();
        color: #333;

        // >* {
        //     max-width: 100%;
        // }

        p,
        .body-text,
        h3,
        h4,
        h5 {
            margin-top: $spacer-5;
            font-size: 1.4rem;

            @include media-breakpoint-up(sm) {
                margin-top: $spacer-5;
                @include font-style(18,26);
            }

        }

        ins {
            text-decoration: none;
        }

        img {
            max-width: 100%;
            margin: auto;
            transform: translate3d(0, 0, 0);
        }

        table {
            margin: auto;
            max-width: 40rem;
        }

        .sub-heading {
            font-family: $sedgwick_Ave_Display;
            font-weight: normal;
            display: block;
            margin-bottom: 10px;
            @include media-breakpoint-up(sm){
                @include font-style(30,50);
            }
        }


        .body-text {
            // @include media-breakpoint-up(md) {
            //     width: 64rem;
            // }


            margin: $spacer-5 auto 0;
            justify-content: space-between;
        }


        .body-image {
            .photo {
                position: relative;
                display: block;
            }

            .caption {
                display: block;
                font-style: italic;
                margin-top: $spacer-3;
                color: #999;
            }

            &.img-fullSize {
                text-align: center;
                @include clearfix();
            }

            &.float-md-left,
            &.float-md-right,
                {
                @include media-breakpoint-up(md) {
                    width: 36rem;
                }

                margin-bottom: $spacer-3;
            }

            @include media-breakpoint-up(md) {

                &.float-md-left,
                    {
                    margin-left: -10rem;
                    margin-right: 3rem;
                }

                &.float-md-right,
                    {
                    margin-right: -10rem;
                    margin-left: 3rem;
                }
            }
        }

        .body-video-youtube {
            text-align: center;

            iframe {
                width: 61rem;
                height: 32rem;
                max-width: 100%;
            }
        }
    }

    .group-img-1 {
        display: flex;
        justify-content: space-between;

        p {
            flex-grow: 1;

            img {
                border: 1px solid #979797;
                border-left: none;
                width: 100%;
            }

            &:first-child {
                flex-grow: 2;

                img {
                    border-left: 1px solid #979797;
                }
            }
        }
    }

    .group-img-2 {
        display: flex;
        justify-content: space-between;

        p {
            width: 25%;

            img {
                border: 1px solid #979797;
                border-left: none;
                width: 100%;
            }

            &:first-child {
                width: 50%;

                img {
                    border-left: 1px solid #979797;
                }
            }
        }
    }

    [class*="group-img"] {
        &+.caption {
            display: block;
            font-style: italic;
            margin-top: 1rem;
            text-align: center;
            color: #999;
        }
    }
}

.article-control {
    display: flex;
    justify-content: space-between;
    height: 16.5rem;
    align-items: center;

    @include media-breakpoint-down(md) {
        height: 10rem;
    }

    border-top: 1px solid $gray-500;
    margin-top: $spacer-5;

    .prev,
    .next,
        {
        display: flex;

        .text {
            display: flex;
            flex-flow: column wrap;
        }

        @include media-breakpoint-up(md) {
            width: 50%;
        }

        span {}

        @include media-breakpoint-down(md) {
            i {
                width: 32px;
                height: 32px;
                background-size: 33px 666px;
            }
        }

        &:hover {
            background-color: rgba(black, .05);
        }
    }

    .prev {
        i {
            margin-right: 20px;

            @include media-breakpoint-down(md) {
                margin-right: 0px;
            }
        }
    }

    .next {
        i {
            margin-left: 20px;

            @include media-breakpoint-down(md) {
                margin-left: 0px;
            }
        }

        span {
            text-align: right;
        }

        flex-direction: row-reverse;
    }

    @include media-breakpoint-down(md) {
        .icon--mix-prev {
            background-position: -0px -83px;
        }

        .icon--mix-next {
            background-position: -0px -116px;
        }

    }

    .main-title {
        font-size: 2.4rem;
        line-height: 3.2rem;

        @include media-breakpoint-up(md) {
            line-height: 4rem;
            margin-top: $spacer-3;
        }

        font-family: $font-family-serif;
        text-transform: uppercase;

        @include media-breakpoint-down(md) {
            font-size: 1.6rem;
        }
    }

    .sub-title {
        font-family: playfair-regular;
        font-size: 1.6rem;

        @include media-breakpoint-down(md) {
            display: none;
        }
    }
}

.menu-verticle {
    left: -320px;
    width: 300px;
    height: 100%;
    overflow: scroll;
    position: fixed;
    top: 50px;

    @include media-breakpoint-up(lg) {
        top: 90px;
    }

    background-color: #fff;
    box-shadow: 5px 0 10px rgba(black, .3);
    z-index: 100;
    padding-bottom: 90px;

    ul {}

    li {
        a {
            display: block;
            padding: 15px;
            border-top: 1px solid $gray-500;
            text-transform: uppercase;
            font-weight: bold;
        }

        &:first-child {
            a {
                border-top: none;
            }
        }
    }

    &.is-active {
        left: 0px;
        transition: left .2s linear;
    }
}

.mask-is-open {
    &:before {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
        background-color: rgba(black, .5);
    }
}